import React from "react";

class TopicContent extends React.Component {
  state = {
    form: {
      title: "",
      description: "",
      amount: ""
    },
    checked: false,
    typed: false,
    error: false
  };

  change = e => {
    const { form } = this.state;
    form[e.target.name] = e.target.value;
    this.setState({ form });
  };

  handleCheck = () => {
    this.setState({
      checked: !this.state.checked
    });
  };

  onContinue = () => {
    this.props.moveNextPage();
  };

  render() {
    const { form, checked } = this.state;
    const {
      onChange,
      title,
      description,
      amount,
      submitted,
      typed
    } = this.props;
    return (
      <div className="topic-content">
        <h2 className="title">Topic Content</h2>
        <p className="paragraph">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Atque sint
          aspernatur, voluptate adipisci ab qui laborum a veniam itaque error
          tempore sit impedit ullam veritatis? Atque nemo sequi cumque
          reiciendis.
        </p>
        <div className="tab-label">
          <label>
            <span>Title</span>
            <input
              type="text"
              placeholder="title"
              name="title"
              value={title}
              className={submitted && !title ? "G-invalid-input" : ""}
              onChange={onChange}
            />
          </label>
        </div>
        <div className="tab-label">
          <label>
            <div className="description-label">
              <span>Description</span>
              <span>min 500 symbols</span>
            </div>

            <textarea
              placeholder="Topic description"
              className={submitted && !description ? "G-invalid-input" : ""}
              name="description"
              value={description}
              onChange={onChange}
            ></textarea>
          </label>
        </div>
        <div className="tab-label">
          <label>
            <span>Rough project budget</span>
            <input
              type="number"
              placeholder="Write amount"
              name="amount"
              value={checked ? "" : amount}
              disabled={checked ? true : false}
              className={
                submitted && !amount
                  ? "G-invalid-input"
                  : checked
                  ? "opacity"
                  : ""
              }
              onChange={onChange}
            />
          </label>
          <div className="check">
            <input
              type="checkbox"
              onChange={this.handleCheck}
              checked={checked}
              disabled={typed ? true : false}
            />
            <span>I am not sure how much amount is required</span>
          </div>
        </div>

        <button className="G-button" onClick={this.onContinue}>
          Continue
        </button>
      </div>
    );
  }
}

export default TopicContent;
